import { Layout, Image, Typography } from "antd";
import { Header, Content } from "antd/lib/layout/layout";
import Sider from "antd/lib/layout/Sider";
import React, { useLayoutEffect, useState } from "react";
import { ISideBarChatroom } from "../interface";

interface Props {
  sideBarChatroom: ISideBarChatroom;
  collapsed: boolean;
}

const SideBarChatroom: React.FC<Props> = ({ sideBarChatroom, collapsed }) => {
  const { Text } = Typography;
  const { participant, latestMessage, unreadMessageCount } = sideBarChatroom;
  const { name, avatarUrl } = participant;
  const { senderId, preview, createdAt } = latestMessage ?? {};
  const [isCollapsed, setIsCollapsed] = useState(false);

  const visibledPreview = preview
    ? preview.length < 30
      ? preview
      : `${preview?.substring(0, 30)}...`
    : "";

  const getUTCTime = (createdAt: number | undefined): string => {
    if (createdAt) {
      const _time = new Date();
      _time.setTime(createdAt);
      return (
        _time.getHours().toString().padStart(2, "0") +
        ":" +
        _time.getMinutes().toString().padStart(2, "0")
      );
    } else {
      return "";
    }
  };

  useLayoutEffect(() => {
    setIsCollapsed(collapsed);
  }, [collapsed]);

  return isCollapsed ? (
    <Image
      width={50}
      height={50}
      src={avatarUrl}
      fallback="data:image/png;base64,"
      preview={false}
      className="avatar-img"
    />
  ) : (
    <Layout className="sideBar-chatroom">
      <Sider
        theme="light"
        width={50}
        className="sideBar-chatroom_avatar-img-container"
      >
        <Image
          width={50}
          height={50}
          src={avatarUrl}
          fallback="data:image/png;base64,"
          preview={false}
          className="avatar-img"
        />
      </Sider>
      <Layout
        className={
          unreadMessageCount > 0
            ? "have-unread-message sideBar-chatroom_avatar-info-container"
            : "sideBar-chatroom_avatar-info-container"
        }
      >
        <Header className="sideBar-chatroom_avatar-info_participant">
          <Text className="sideBar-chatroom_name">{name}</Text>
          <Text className="latest-message-time">{getUTCTime(createdAt)}</Text>
        </Header>
        <Content className="sideBar-chatroom_latest-message-container">
          <Text>
            {senderId === 0 ? `you: ${visibledPreview}` : visibledPreview}
          </Text>
        </Content>
      </Layout>
      {unreadMessageCount > 0 ? (
        <div className="unread-message-count-container">
          <Text>{unreadMessageCount}</Text>
        </div>
      ) : (
        ""
      )}
    </Layout>
  );
};

export default SideBarChatroom;
